<template>
  <div class="tab-bar-item" @click="itemClick" :style="activeStyle" :class="activeBefore">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "TabBarItem",
  props: {
    path: String,
    activeColor: {
      type: String,
      default: 'red'
    }
  },
  data() {
    return {
      
    }
  },
  computed: {
    activeBefore() {
      return this.$route.path === this.path ? 'activeBefore' : ''
    },
    activeStyle() {
      return this.$route.path === this.path ? {color: this.activeColor} : {}
    }
  },
  methods: {
    itemClick() {
      this.$router.push(this.path)
    }
  }
}
</script>

<style scoped>
  .tab-bar-item{
    display: inline-block;
    cursor: pointer;
    position: relative;
    margin-right: 10px;
  }
  .activeBefore::before{
    content: "";
    display: inline-block;
    width: 80%;
    height: 5px;
    background-color: var(--color);
    position: absolute;
    left: 10%;
    bottom: -5px;
    border-radius: 2.5px;
    z-index: -1;
  }
</style>
